.NavigationLink {
  margin-right: 15px;
  padding: 8px;
  border-radius: 2px;
  background-color: #3f51b5;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  text-align: center;
  display: inline-block;
  color: #fff;
  border: 0;
  text-decoration: none;
  cursor: pointer;
  font-family: inherit;
  font-size: 22px;
  line-height: 24px;
  font-style: normal;
  font-weight: 500;
  min-width: 150px;
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
    0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}

.NavigationLink:hover,
.NavigationLink:focus {
  background-color: #172aa7;
}

.Active {
  background-color: orange;
  color: black;
}

.Active:hover,
.Active:focus {
  background-color: rgb(214, 76, 13);
  color: black;
}
